import styled from '@emotion/styled';
import { ColorStyle, NotoSerif, FontWeight } from '../common/variable';

export const FaqStyle = styled.div`
  padding:180px 0 80px;
  background-color:${ColorStyle.blue};
`

export const Title = styled.h2`
    margin-bottom:4rem;
    font-size:3rem;
    font-weight:${FontWeight.bold};
    ${NotoSerif};
`

export const FaqAcc = styled.div`
  border-top:2px solid #000;
  .header{
    position:relative;
    padding:3rem 0;
    padding-right:40px;
    border-bottom:1px solid #bbc4d7;
    color:#0c1622;
    font-size:2rem;
    cursor:pointer;
    &:after{
      content:"";
      position:absolute;
      top:28px;
      right:14px;
      width:14px;
      height:14px;
      border-bottom:2px solid #0c1622;
      border-right:2px solid #0c1622;
      transform:rotate(45deg);
    }
    &.on:after{
      top:36px;
      transform:rotate(-138deg);
    }
  }
  .cont{
    border-bottom:1px solid #bbc4d7;
    padding:3rem 0;
    color:${ColorStyle.GreyTxt};
    font-size:1.6rem;
    line-height:1.6;
  }

`