import { useNavigate } from "react-router-dom";
import { Header } from "../CommonLayout/Header";
import { Search } from "../common/Search";
import { ContWidth } from "../common/common.style";
import { Button } from "./Button";
import { ContTopText } from "./ContTopText";
import { ObiItem } from "./ObiItem";
import { ObiContentStyle, Title, ContTop, ContGrid } from "./memorial.style"
import imgMemorial1 from '../images/sample/img_memorial1.jpg';
import imgMemorial2 from '../images/sample/img_memorial2.jpg';
import imgMemorial3 from '../images/sample/img_memorial3.jpg';

export const Obituary = () => {
    const navigate = useNavigate();
    const linkpath = () => {
        navigate("/obiView");
    };
    return(
        <>
            <Header classname="contHeader"/>
            <ObiContentStyle>
                <ContWidth>
                    <Title>부고</Title>
                    <ContTop>
                        <ContTopText num="43"/>
                        <div className="d_flex">
                            <Search />  
                            <Button classname="base" buttnText="부고 등록"/>
                        </div>
                    </ContTop>
                    <ContGrid>
                        <ObiItem 
                            imgSrc={imgMemorial1}
                            imgAlt="김명숙 이미지" 
                            name="김명숙" 
                            date="1934-03-30 ~ 2022-08-20"
                            clickEv={linkpath}
                            type="obiType"
                        /> 
                        <ObiItem 
                            imgSrc={imgMemorial2}
                            imgAlt="김한국 이미지" 
                            name="김한국" 
                            date="1934-03-30 ~ 2022-08-20"
                            clickEv={linkpath}
                            type="obiType"
                        /> 
                        <ObiItem 
                            imgSrc={imgMemorial3}
                            imgAlt="윤영환 명예회장 이미지" 
                            name="윤영환 명예회장" 
                            date="1934-03-30 ~ 2022-08-20"
                            clickEv={linkpath}
                            type="obiType"
                        /> 
                        <ObiItem 
                            imgSrc={imgMemorial1}
                            imgAlt="김명숙 이미지" 
                            name="김명숙" 
                            date="1934-03-30 ~ 2022-08-20"
                            clickEv={linkpath}
                            type="obiType"
                        /> 
                    </ContGrid>
                </ContWidth>
            </ObiContentStyle>
        </>
    )
}