import styled from '@emotion/styled';
import {MediaWidth, BgStyle, ColorStyle} from "./variable.jsx"
import IcoSchBlue from '../images/ico_search_blue.png';
import IcoSchWhite from '../images/ico_search_white.png';

export const ContWrap=styled.div`
   position:relative;
   background:#eef8fb;
    @media (max-width: ${MediaWidth.tablet}) {
        width:100%;
    }
`
export const ContWidth=styled.div`
    max-width:1400px;
    margin:0 auto;
    @media (max-width: ${MediaWidth.pc}) {
        width:94vw;
    }
`
export const SearchForm=styled.div`
    position:relative;
    width:52rem;
    .search_input{
        width:100%;
        padding:8px 20px 8px 0;
        font-size:2rem;
        background-color:transparent;   
        border-bottom:2px solid #0c1622;
        box-sizing:border-box;
    }
    .ico_sch{
        --icoSize:34px;
        width:var(--icoSize);
        height:var(--icoSize);
        display:inline-block;
        position:absolute;
        top:50%;
        transform:translateY(-50%);
        right:0;
        margin-top:-3px;
        ${BgStyle.size100};
        background-image:url(${IcoSchBlue});
        text-indent:-9999em;
    }   
    &.round{
        width:24rem;
        .search_input{
            line-height:6rem;
            padding: 0 6rem 0 20px;
            color:#fff;
            border-bottom:none;
            border:2px solid rgba(255,255,255,.5);
            border-radius:50px;
            &::placeholder{color:#fff;}
        }
        .ico_sch{
            margin-top:0;
            background-image:url(${IcoSchWhite});
            right:1.8rem;
        }
    }
  @media (max-width: ${MediaWidth.tablet}) {
    .ico_sch{
      --icoSize:28px;
    }
    &.round{
      width:20rem;
    }
  }
`
export const PcBlock=styled.span`
    display:block;
    @media (max-width: ${MediaWidth.tablet}) {
        display:inline;
    }
`
export const MbBlock=styled.span`
    @media (max-width: ${MediaWidth.tablet}) {
        display:block;
    }
`
export const Blind = styled.span`
  display:block;
  overflow:hidden;
  line-height:0;
  text-indent:-9999px
`
export const BtnClose = styled.button`
  position:absolute;
  &:before,&:after{
    content:"";
    display:block;
    background-color:#fff;
  }
  &:before{
    transform: rotate(45deg);
  }
  &:after{
    margin-top:-12px;
    transform:rotate(-45deg);
  }
`
export const NotFoundStyle = styled.div`
  padding:80px 0;
  text-align:center;
  font-size:5.6rem;
  a{
    display:block;
    line-height:40px;
    border-radius:50px;
    width:240px;
    margin:60px auto;
    background:${ColorStyle.Navy};
    color:#fff;
  }
`
;
