 import React, { useEffect } from 'react';
import $ from 'jquery';

export const FaqBoxJq = ({title,cont}) => {
  useEffect(() => {
    $(".cont").slideUp();
    const handleAccordionClick = function () {
      $(this).next(".cont").stop().slideToggle(300);
      $(this).toggleClass('on').siblings().removeClass('on');
      $(this).next(".cont").siblings(".cont").slideUp(300); // 1개씩 펼치기
    };

    $(".header").on('click', handleAccordionClick);

    // Cleanup function to remove the event handler
    return () => {
      $(".header").off('click', handleAccordionClick);
    };
  }, []);

  return (
    <>
      <div className="header">{title}</div>
      <div className="cont">{cont}</div>
    </>
  )
}